const actions = {
  getGLCurrencies: () => ({
    type: 'GET_GL_CURRENCIES_REQUEST',
  }),
  updateGLCurrency: (id_currency, data) => ({
    type: 'UPDATE_GL_CURRENCY_REQUEST',
    payload: {
      id_currency: id_currency,
      body: data
    }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};


export default actions;
