import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
// import { Redirect } from "react-router-dom";

import CurrencyForm from "./updateCurrencyForm";

import { Tabs, Divider } from "antd";

import Button from "../../../../components/uielements/button";
import notification from "../../../../components/notification";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
// import Button from "../../../../components/uielements/button";
import { Box } from "../../../App/app.style";
import actions from "../../../../redux/generalLedger/currency/actions";

const TabPane = Tabs.TabPane;
const redirectTo = "/dashboard/setup/menus";

class UpdateGeneralLedger extends Component {
  componentDidMount = () => {
    const { getGLCurrencies } = this.props;
    getGLCurrencies();
  };

  componentDidUpdate = () => {
    //Display error notif
    if (this.props.generalLedgerCurrency.error) {
      notification("error", this.props.generalLedgerCurrency.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.generalLedgerCurrency.success) {
      notification("success", this.props.generalLedgerCurrency.success);
      this.props.clearSuccess();
    }
  };

  render() {
    //const general_ledger = this.props.currency.currency ? this.props.currency.currency : []
    // console.log(this.props.generalLedgerCurrency);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="generalLedgers.currency.title" />}
            style={{ margin: 0 }}
          >
            <Button
              onClick={() => this.props.history.replace(redirectTo)}
              style={{ marginLeft: "5px" }}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>
          {/* <Divider /> */}
          <CurrencyForm />
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return { generalLedgerCurrency: state.generalLedgerCurrency };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(UpdateGeneralLedger);
