import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

//import moment from "moment";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Row, Col, Select, Spin, Divider } from "antd";
import actions from "../../../../redux/generalLedger/currency/actions";
import getLanguage from "../../../App/MyLanguange";

const { Option } = Select;
// const { TextArea } = Input;
// const InputGroup = Input.Group;

class GeneralLedgerCurrencyForm extends Component {

  handleChangeCurrency = (value) => {
    const {setFieldsValue} = this.props.form 
    this.props.generalLedgerCurrency.currencies.map(data => {
      if(data.id_currency === value){
        setFieldsValue({
          ["id_account_receivable"]: data.currency_account.id_account_receivable,
          ["id_account_payable"]: data.currency_account.id_account_payable,
          ["id_account_invoice_discount"]: data.currency_account.id_account_invoice_discount,
          ["id_account_realize_gain"]: data.currency_account.id_account_realize_gain,
          ["id_account_realize_loss"]: data.currency_account.id_account_realize_loss,
          ["id_account_unrealize_gain"]: data.currency_account.id_account_unrealize_gain,
          ["id_account_unrealize_loss"]: data.currency_account.id_account_unrealize_loss,
        }) 
      }
          
    })
  }

  handleSubmit = e => {
    //console.log("Masuk Pak")
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, value) => {
      if (!err) {
        const { updateGLCurrency } = this.props;
        // console.log(values);
        updateGLCurrency(value.id_currency, value);
      }
    });
  };


  render() {
    // console.log(this.props.generalLedgerCurrency);
    const { getFieldDecorator } = this.props.form;
    const { generalLedgerCurrency } = this.props;
    const { loading, loadingSubmit } = generalLedgerCurrency;
    const data = generalLedgerCurrency.currencies ? generalLedgerCurrency.currencies : [];

    // const used = data.is_used;
    // const error = generalLedgerCurrency.errorField ? generalLedgerCurrency.errorField : [];
    // let opening_balance=null;
    // let retained_earning=null;


    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    return (
      <div>
        {this.props.generalLedgerCurrency.loading  ? (
            <div className="spinStyle">
              <Spin size="large" />
            </div>
          ) : (

      <Form layout="vertical" onSubmit={this.handleSubmit}>
        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}>
            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="currencies.pageHeader" />}
            >
              {getFieldDecorator("id_currency", {
                // initialValue: ,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Select
                  loading={generalLedgerCurrency.loading}
                  showSearch
                  optionFilterProp="children"
                  onChange={this.handleChangeCurrency}
                  placeholder={getLanguage({ id: "noCurrency"})}
                >
                  {this.props.generalLedgerCurrency.currencies.map((data, key) => (
                    <Option key={key} value={data.id_currency}>
                      {data.currency_code + " (" + data.currency_name +")"}
                    </Option>
                  ))}
                </Select>
              )}
            </Form.Item>
          </div>
        </div>

        <Divider orientation="left" style={{ fontSize: 14 }}>
          <IntlMessages id="currencies.add.subtitle.default" />
        </Divider>

        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}>
            <Form.Item
              {...formItemLayout}
              label={
                <IntlMessages id="currencies.add.realizeGainAccount" />
              }
            >
              {getFieldDecorator("id_account_realize_gain", {
                //initialValue: data.currency_account.id_account_realize_gain,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Select
                  loading={generalLedgerCurrency.loading}
                  showSearch
                  optionFilterProp="children"
                  //disabled={used ? true : false}
                >
                  {this.props.generalLedgerCurrency.accounts.map((data, key) =>
                    data.id_account_type === "17"
                    ? (
                      <Option key={key} value={data.id_account}>
                        {data.account_code + " " + data.account_name}
                      </Option>
                    ) : null
                  )}
                </Select>
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={
                <IntlMessages id="currencies.add.realizeLossAccount" />
              }
            >
              {getFieldDecorator("id_account_realize_loss", {
                //initialValue: data.currency_account.id_account_realize_loss,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Select
                  loading={generalLedgerCurrency.loading}
                  showSearch
                  optionFilterProp="children"
                  //disabled={used ? true : false}
                >
                  {this.props.generalLedgerCurrency.accounts.map((data, key) =>
                    data.id_account_type === "18"
                    ? (
                      <Option key={key} value={data.id_account}>
                        {data.account_code + " " + data.account_name}
                      </Option>
                    ) : null
                  )}
                </Select>
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={
                <IntlMessages id="currencies.add.unrealizeGainAccount" />
              }
            >
              {getFieldDecorator("id_account_unrealize_gain", {
                //initialValue: data.currency_account.id_account_unrealize_gain,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Select
                  loading={generalLedgerCurrency.loading}
                  showSearch
                  optionFilterProp="children"
                  //disabled={used ? true : false}
                >
                  {this.props.generalLedgerCurrency.accounts.map((data, key) =>
                    data.id_account_type === "17"
                    ? (
                      <Option key={key} value={data.id_account}>
                        {data.account_code + " " + data.account_name}
                      </Option>
                    ) : null
                  )}
                </Select>
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={
                <IntlMessages id="currencies.add.unrealizeLossAccount" />
              }
            >
              {getFieldDecorator("id_account_unrealize_loss", {
               // initialValue: data.currency_account.id_account_unrealize_loss,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Select
                  loading={generalLedgerCurrency.loading}
                  showSearch
                  optionFilterProp="children"
                  //disabled={used ? true : false}
                >
                  {this.props.generalLedgerCurrency.accounts.map((data, key) =>
                    data.id_account_type === "18"
                    ? (
                      <Option key={key} value={data.id_account}>
                        {data.account_code + " " + data.account_name}
                      </Option>
                    ) : null
                  )}
                </Select>
              )}
            </Form.Item>
          </div>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 0 0 15px" }}>
            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="currencies.add.receivableAccount" />}
            >
              {getFieldDecorator("id_account_receivable", {
                // initialValue: data.currency_account.id_account_receivable,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Select
                  loading={generalLedgerCurrency.loadingFilterAccounts}
                  showSearch
                  optionFilterProp="children"
                  //disabled={used ? true : false}
                >
                  {this.props.generalLedgerCurrency.accounts.map((data, key) => (
                    data.id_account_type === "03"
                    ? (<Option key={key} value={data.id_account}>
                        {data.account_code + " (" + data.account_name +")"}
                      </Option>)
                    : null
                  ))}
                </Select>
              )}
            </Form.Item>
          
            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="currencies.add.payableAccount" />}
            >
              {getFieldDecorator("id_account_payable", {
                // initialValue: data.currency_account.id_account_payable,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Select
                  loading={generalLedgerCurrency.loadingFilterAccounts}
                  showSearch
                  optionFilterProp="children"
                  //disabled={used ? true : false}
                >
                  {this.props.generalLedgerCurrency.accounts.map((data, key) => (
                    data.id_account_type === "09"
                    ? (<Option key={key} value={data.id_account}>
                        {data.account_code + " (" + data.account_name + ")"}
                      </Option>)
                    : null
                  ))}
                </Select>
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="currencies.add.invoiceDiscountAccount" />}
            >
              {getFieldDecorator("id_account_invoice_discount", {
              // initialValue: data.currency_account.id_account_invoice_discount,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Select
                  loading={generalLedgerCurrency.loadingFilterAccounts}
                  showSearch
                  optionFilterProp="children"
                  ////disabled={used ? true : false}
                >
                  {this.props.generalLedgerCurrency.accounts.map((data, key) => (
                    data.id_account_type==="14"
                    ? (<Option key={key} value={data.id_account}>
                        {data.account_code + " " + data.account_name}
                      </Option>)
                    : null
                  ))}
                </Select>
              )}
            </Form.Item>

          </div>
        </div>

        <Row>
          <Col>
          <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{ marginTop: "20px", marginLeft: "50px" }}
            loading={loading || loadingSubmit}
          >
            <IntlMessages id="button.submit" />
          </Button>
        </Form.Item>
          </Col>
        </Row>

        
      </Form>
      )}
      </div>
    );
  }
}

const WrappedGeneralLedgerCurrencyForm = Form.create({
  name: "general_ledger_currency"
})(GeneralLedgerCurrencyForm);

//export default WrappedGeneralLedgerCurrencyForm;

const mapStateToProps = state => {
  return { generalLedgerCurrency: state.generalLedgerCurrency };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(WrappedGeneralLedgerCurrencyForm);
